import Students from './student'
class Sort{
    reOrderTests(exam) {
        function processTime(time) {
            let time1 = time.split(':')
            let time2 = time1[0].split('-')
            return (time2[0] + time2[1] + time2[2] + time1[1])
        }
        let testArray = []
        let timeStem = []
        let testName = []
        let arrangedTests = {}
        let temp
        Object.keys(exam).forEach(thisTest => {
            testArray.push(exam[thisTest])
            testName.push(thisTest)
        })
        for (let i = 1; i < testArray.length; ++i) {
            for (let j = 0; j < testArray.length - i; ++j) {
                timeStem[0] = parseInt(processTime(testArray[j].date + testArray[j].time))
                timeStem[1] = parseInt(processTime(testArray[j + 1].date + testArray[j + 1].time))
                if (timeStem[0] < timeStem[1]) {
                    temp = testArray[j]
                    testArray[j] = testArray[j + 1]
                    testArray[j + 1] = temp

                    temp = testName[j]
                    testName[j] = testName[j + 1]
                    testName[j + 1] = temp
                }
            }
        }
        temp = 0
        testArray.forEach(thisTest => {
            arrangedTests ={ ...arrangedTests,[testName[temp++]]:thisTest}
        }) 
        return arrangedTests
    }

    reOdrderStudent(testDetails) {
        let studentName = []
        let studentMarks = []
        let arranged = {}
        let flag=0
        Object.keys(testDetails).forEach(stud => {
            if (stud !== 'ANONYMOUS' && stud !== 'date' && stud !== 'time' && stud !== 'maxMarks') {
                studentName.push(stud)
                studentMarks.push(testDetails[stud])
                if (testDetails[stud])
                    flag = 1;
            }
        })
        if (flag){let temp
        for (let i = 1; i < studentMarks.length; ++i) {
            for (let j = 0; j < studentMarks.length - i; ++j){
                if (studentMarks[j] < studentMarks[j + 1]) {
                    temp = studentMarks[j]
                    studentMarks[j] = studentMarks[j + 1]
                    studentMarks[j + 1] = temp

                    temp = studentName[j]
                    studentName[j] = studentName[j + 1]
                    studentName[j + 1] = temp
                }
            }
        }
        for (let i = 0; i < studentMarks.length; ++i)
            arranged={...arranged, [studentName[i]]:studentMarks[i]}
        Students.setStudents(arranged)}
    }
}
export default new Sort()